@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";

  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/mono-regular.woff2") format("woff2"),
    url("../../fonts/mono-regular.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/roboto-condensed-regular.woff2") format("woff2"),
    url("../../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/roboto-condensed-bold.woff2") format("woff2"),
    url("../../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: swap;
}
